@import url('https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap');

/* base styles */
* {
  margin: 0;
  font-family: "Quicksand";
  color: #333;
}

.blogList em {
  color: #808080;
  margin: 0;
}

.blogList button {
  color: #f2f2f2;
  background-color: #808080;
  border-color: #333;
  border-radius: 8px;
  margin: 0;
  display: block;
  margin-left: auto;
}

.blogList button:hover {
  color: #f2f2f2;
  background-color: #f1356d;
  border-color: #333;
  border-radius: 8px;
  margin: 0;
  display: block;
  margin-left: auto;
}

.navbar {
  padding: 20px;
  display: flex;
  align-items: center;
  max-width: 600px;
  margin: 0 auto;
  border-bottom: 1px solid #f2f2f2;
}

.navbar h1 {
  color: #f1356d;
}
.navbar .links {
  margin-left: auto;
}
.navbar a {
  margin-left: 16px;
  text-decoration: none;
  padding: 6px;
}
.navbar a:hover {
  color: #f1356d;
}

.content {
  max-width: 600px;
  margin: 40px auto;
  padding: 20px;
}

/* blog previews / list */
.blogPreview {
  padding: 10px 16px;
  margin: 20px 0;
  border-bottom: 1px solid #fafafa;
}

.blogPreview:hover {
  box-shadow: 1px 3px 5px rgba(0,0,0,0.1);
}

.blogPreview h2 {
  font-size: 20px;
  color: #f1356d;
  margin-bottom: 8px;
}

.blogPreview a {
  text-decoration: none;
}